import TextField from '@mui/material/TextField';

const TextBoxMulti = ({ label, helperText, value, onChange, rows, ...textFieldProps }) => {
 
  return (
    <TextField
      label={label}
      variant="outlined"
      fullWidth
      multiline
      rows={rows}
      helperText={helperText}
      value={value}
      onChange={onChange}
      {...textFieldProps}
    />
  );
};

export default TextBoxMulti;