import React from 'react';
import { FormControlLabel, Switch } from '@mui/material';

const CheckBox = ({ label, checked, onChange, ...checkboxProps }) => {
  return (
    <FormControlLabel
      control={
        <Switch
          checked={checked}
          onChange={onChange}
          {...checkboxProps}
        />
      }
      label={label}
    />
  );
};

export default CheckBox;
