import React, { useState, useEffect } from 'react';
import Container from "../../Container/Container";
import Typography from '@mui/material/Typography';
import TextBox from "../../mui/textbox/TextBoxNormal";
import CheckBox from "../../mui/checkbox/checkbox";
import SpaceDivider from "../../mui/tools/divider";
import StringlistEditor from "../../mui/modal/StringlistEditor";
import Styledbox from "../../mui/tools/styledbox";
import Button from '@mui/material/Button';
import StyleHR from "../../mui/tools/styleHr";
import LoadingSpinner from '../../LoadingSpinner/LoadingSpinner';
import { Alert, AlertTitle } from '@mui/material';
import SimpleModal from '../../mui/modal/Dialog';
import Footer from "../../Footer/Footer";


const KonfigurationsKomponente = () => {

   

    const [konfiguration, setKonfiguration] = useState(null);
    const [alert, setAlert] = useState('');
    const [alertState, setAlertState] = useState(false);
    const [open, setOpen] = useState(false);
    const [dialog, setDialog] = useState('');

    // Konfigurationsobjekt
    useEffect(() => {
        const fetchKonfiguration = async () => {
            try {
                const response = await fetch(localStorage.getItem('host') + '/config_global', {
                    method: 'GET',
                    headers: {
                        'Content-Type': 'application/json',
                        'token': localStorage.getItem('token') 
                      
                    }
                });
                if (!response.ok) {
                    setAlertState(true);
                    setAlert(`HTTP error! status: ${response.status}`);
                    throw new Error(`HTTP error! status: ${response.status}`);
                } else {
                    setAlertState(false);
                    setAlert('');
                }
                const json = await response.json();
                setKonfiguration(json);
            } catch (error) {
                setAlertState(true);
                setAlert(`Ein Fehler ist aufgetreten beim Abrufen der Konfiguration: ${error}`);
                console.error("Ein Fehler ist aufgetreten beim Abrufen der Konfiguration:", error);
            }
        };

        fetchKonfiguration();
    }, []); // Leeres Array, um den Effekt nur beim ersten Rendern auszuführen

    if (!konfiguration) {
        return (<Container title=""><LoadingSpinner></LoadingSpinner>

            {alertState && (
                <p>
                    <Alert severity="error">
                        <AlertTitle>Error</AlertTitle>
                        {alert}
                    </Alert>
                </p>
            )}



        </Container>);
    } else {
        const handleClose = () => {
            setDialog('');
            setOpen(false);
        };

        // Funktion zur Umwandlung des Konfigurationsobjekts in eine Tabelle
        const updateMeticWhitelist = (newList) => {
            setKonfiguration({ ...konfiguration, metricWhitelist: newList });
        };

        const updateRateWhitelist = (newList) => {
            setKonfiguration({ ...konfiguration, rateWhitelist: newList });
        };

        const updateCorsAllowOrigins = (newList) => {
            setKonfiguration({ ...konfiguration, corsAllowOrigins: newList });
        };
        const updateCorsAllowMethods = (newList) => {
            setKonfiguration({ ...konfiguration, corsAllowMethods: newList });
        };
        const updateCorsAllowHeaders = (newList) => {
            setKonfiguration({ ...konfiguration, corsAllowHeaders: newList });
        };

        const HandleSave = () => {

            fetch(localStorage.getItem('host') + '/config_global', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'token': localStorage.getItem('token')
                },
                body: JSON.stringify(konfiguration),
            }).then(response => response.json())
                .then(data => {
                    console.log('Success:', data);
                    setDialog("Erfolgreich gespeichert");
                    setOpen(true);
                    ;
                })
                .catch((error) => {
                    console.error('Error:', error);
                    setDialog("Fehler beim Speichern");
                    setOpen(true);
                    alert("Fehler beim Speichern", error);
                });

        }





        return (<Container title="Konfiguration">
            <Typography variant="h4" gutterBottom component="div">
                Gateway Basic Configuration
            </Typography>
            <SpaceDivider /><SpaceDivider />
            <TextBox
                label="Gateway Name"
                helperText="Der Name deines Gateways"
                value={konfiguration.name}
                onChange={(e) => {
                    const updatedConf = { ...konfiguration };
                    updatedConf.name = e.target.value;
                    setKonfiguration(updatedConf);
                }}
                required
            /><SpaceDivider />
            <TextBox
                label="Prefix"
                helperText=""
                value={konfiguration.prefix}
                onChange={(e) => {
                    const updatedConf = { ...konfiguration };
                    updatedConf.prefix = e.target.value;
                    setKonfiguration(updatedConf);
                }}
                required
            /><SpaceDivider />
            <CheckBox
                label="Debug"
                checked={konfiguration.debug}
                onChange={(e) => {
                    const updatedConf = { ...konfiguration };
                    updatedConf.debug = e.target.checked;
                    setKonfiguration(updatedConf);
                }}
            /><SpaceDivider />
            <TextBox
                label="HTTP Port"
                helperText=""
                value={konfiguration.port}
                onChange={(e) => {
                    const updatedConf = { ...konfiguration };
                    updatedConf.port = e.target.value;
                    setKonfiguration(updatedConf);
                }}

                required
            /><SpaceDivider />

            <TextBox
                label="HTTPS Port"
                helperText=""
                value={konfiguration.sslPort}
                onChange={(e) => {
                    const updatedConf = { ...konfiguration };
                    updatedConf.sslPort = e.target.value;
                    setKonfiguration(updatedConf);
                }}


            /><SpaceDivider />
            <Styledbox>

                <CheckBox
                    label="Export Log"
                    checked={konfiguration.exportLog}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.exportLog = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.exportLog && (
                    <>

                        <SpaceDivider />
                        <TextBox
                            label="Export Log Path"
                            helperText="Pfad zum Log"
                            value={konfiguration.exportLogPath}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.exportLogPath = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />

                    </>
                )}
            </Styledbox>
            <SpaceDivider />
            <Styledbox>

                <CheckBox
                    label="Host Name Check"
                    checked={konfiguration.hostnamecheck}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.hostnamecheck = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.hostnamecheck && (
                    <>

                        <SpaceDivider />
                        <TextBox
                            label="Hostname"
                            helperText="Domain tld oder IP adress"
                            value={konfiguration.hostname}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.hostname = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />

                    </>
                )}
            </Styledbox>
            <SpaceDivider />
            <Styledbox>
                <CheckBox
                    label="SSL"
                    checked={konfiguration.ssl}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.ssl = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.ssl && (
                    <>

                        <SpaceDivider />
                        <TextBox
                            label="Pem CRT Pfad"
                            helperText="Pfad zur .pem CRT Datei"
                            value={konfiguration.pemCrt}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.pemCrt = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />
                        <SpaceDivider />
                        <TextBox
                            label="Pem Key Pfad"
                            helperText="Pfad zur .pem Key Datei"
                            value={konfiguration.pemKey}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.pemKey = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />




                    </>
                )}
            </Styledbox>
            <SpaceDivider />
            <Styledbox>
                <CheckBox
                    label="Rate Limiter"
                    checked={konfiguration.rate}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.rate = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.rate && (
                    <>
                        <SpaceDivider />
                        <TextBox
                            label="Rate"
                            helperText="Connect rate per IP * Window"
                            value={konfiguration.rate.rate}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.rate.rate = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />
                        <SpaceDivider />
                        <TextBox
                            label="Window"
                            helperText="Minutes"
                            value={konfiguration.rate.window / 1_000_000_000 / 60}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.rate.window = e.target.value * 1_000_000_000 * 60;
                                setKonfiguration(updatedConf);
                            }}

                            required
                        />
                        <SpaceDivider />

                        <StringlistEditor whitelist={konfiguration.rateWhitelist || []} setWhitelist={updateRateWhitelist} modalcaption={"Neue IP hinzufügen"} caption={"Rate Whitelist"} />
                    </>
                )}
            </Styledbox>
            <SpaceDivider />
            <Styledbox>
                <CheckBox
                    label="Metrics"
                    checked={konfiguration.metric}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.metric = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.metric && (
                    <>
                        <SpaceDivider />
                        <TextBox
                            label="Metric Path"
                            helperText="GET metrics path url"
                            value={konfiguration.metricPath}
                            onChange={(e) => {
                                const updatedConf = { ...konfiguration };
                                updatedConf.metricPath = e.target.value;
                                setKonfiguration(updatedConf);
                            }}
                            required
                        />
                        <SpaceDivider />


                        <StringlistEditor whitelist={konfiguration.metricWhitelist} setWhitelist={updateMeticWhitelist} modalcaption={"Neue IP hinzufügen"} caption={"Metrics Whitelist"} />
                    </>
                )}
            </Styledbox>
            <SpaceDivider />


            <Styledbox>
                <CheckBox
                    label="Cors System"
                    checked={konfiguration.cors}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.cors = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                <CheckBox
                    label="Cors Advanced"
                    checked={konfiguration.corsAdvanced}
                    onChange={(e) => {
                        const updatedConf = { ...konfiguration };
                        updatedConf.corsAdvanced = e.target.checked;
                        setKonfiguration(updatedConf);
                    }}
                /><SpaceDivider />
                {konfiguration.corsAdvanced && (
                    <>
                        <SpaceDivider />
                        <StyleHR />
                        <StringlistEditor whitelist={konfiguration.corsAllowOrigins} setWhitelist={updateCorsAllowOrigins} modalcaption={"AllowOrigin"} caption={"Allow Origins"} />
                        <StyleHR />
                        <StringlistEditor whitelist={konfiguration.corsAllowMethods} setWhitelist={updateCorsAllowMethods} modalcaption={"AllowMethod"} caption={"Allow Methods"} />
                        <StyleHR />
                        <StringlistEditor whitelist={konfiguration.corsAllowHeaders} setWhitelist={updateCorsAllowHeaders} modalcaption={"corsAllowHeader"} caption={"Allow Headers"} />

                    </>
                )}
            </Styledbox>
            <SpaceDivider />
            <Styledbox>
                <Button variant="contained" onClick={HandleSave}>Konfiguration speichern</Button>
            </Styledbox>

            <SimpleModal
                open={open}
                onClose={handleClose}
                caption="Hinweis"
                text={dialog}
            />
              <Footer></Footer>
        </Container>)
    };
};

export default KonfigurationsKomponente;
