import React from "react";
import "./NavBar.css";

import {useNavigate, useLocation} from "react-router-dom";
import {Button} from "@mui/material";


function NavBar(props) {
    const navigate = useNavigate();
    const location = useLocation();

    function handleLogout() {
        localStorage.removeItem("token"); // Löschen Sie das Token aus dem localStorage
        navigate("/"); // Navigiere zurück zur Login-Seite (angenommen, die Login-Seite ist unter der Root-Route)
    }

    function handleClick(site) {
        navigate(site); // Navigiere zur angegebenen Route
    }

    function getButtonClassName(route) {
        return location.pathname === route ? "active" : "inactive";
    }

    return (
        <div>
            <div className="navbar-container">


                <Button
                    className={`button-base ${getButtonClassName("/web/dashboard")}`}
                    onClick={() => handleClick("/web/dashboard")}

                    fullWidth
                    variant="outlined"
                    active={getButtonClassName("/web/dashboard")}
                >Dashboard</Button>
                <Button
                    className={`button-base ${getButtonClassName("/web/gateway")}`}
                    onClick={() => handleClick("/web/gateway")}

                    fullWidth
                    variant="outlined"
                    active={getButtonClassName("/web/gateway")}
                >Gateway</Button>
           
                <Button
                    className={`button-base ${getButtonClassName("/web/endpoints")}`}
                    onClick={() => handleClick("/web/endpoints")}

                    fullWidth
                    variant="outlined"
                    active={getButtonClassName("/web/endpoints")}
                >Endpoints</Button>

<Button
                    className={`button-base ${getButtonClassName("/lweb/ogs")}`}
                    onClick={() => handleClick("/web/logs")}

                    fullWidth
                    variant="outlined"
                    active={getButtonClassName("/web/logs")}
                >Logs</Button>
                <Button
                    className={`button-base ${getButtonClassName("/web/events")}`}
                    onClick={() => handleClick("/web/events")}

                    fullWidth
                    variant="outlined"
                    active={getButtonClassName("/web/events")}
                >Events</Button>

                <Button

                    onClick={handleLogout}
                    fullWidth
                    variant="outlined"

                >Logout</Button>

            </div>
            <div>


            </div>
        </div>
    );
}

export default NavBar;
