import React, { useState } from 'react';
import { Button, IconButton, TextField, Dialog, DialogActions, DialogContent, DialogTitle, Table, TableBody, TableCell, TableRow, TableContainer, Paper, Typography } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';

const StringlistEditor = ({ whitelist , setWhitelist, modalcaption, caption }) => {
  //const [whitelist, setWhitelist] = useState(whitelistProp || []);
  const [newEntry, setNewEntry] = useState('');
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setNewEntry(''); // Textfeld leeren beim Schließen des Dialogs
  };

  const handleAddEntry = () => {
    if (newEntry && !whitelist.includes(newEntry)) {
      setWhitelist([...whitelist, newEntry]);
      handleClose();
    }
  };

  const handleRemoveEntry = (entryToRemove) => {
    setWhitelist(whitelist.filter(entry => entry !== entryToRemove));
  };

  return (
    <div>
      <Typography variant="h8" style={{ margin: '10px' }}>{caption}</Typography>
      <TableContainer component={Paper} style={{ marginBottom: '10px' }}>
        <Table aria-label="whitelist table" size="small">
          <TableBody>
            {whitelist.map((entry, index) => (
              <TableRow key={index}>
                <TableCell>{entry}</TableCell>
                <TableCell align="right">
                  <IconButton edge="end" aria-label="delete" onClick={() => handleRemoveEntry(entry)}>
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Button startIcon={<AddIcon />} onClick={handleClickOpen} variant="contained" size="small">
        Neuer Eintrag
      </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{modalcaption}</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Eintrag"
            type="text"
            fullWidth
            variant="outlined"
            value={newEntry}
            onChange={(e) => setNewEntry(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Abbrechen</Button>
          <Button onClick={handleAddEntry}>Hinzufügen</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default StringlistEditor;
