import React, { useState, useEffect } from 'react';
import TextField from '@mui/material/TextField';

const isValidEmail = (email) => {
  return /\S+@\S+\.\S+/.test(email);
};

const isValidNumber = (number) => {
  return !isNaN(number);
};

const isValidIP = (ip) => {
  return /^(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.(25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/.test(ip);
};



const TextBoxNormal = ({ label, helperText, value, onChange, validationType, ...textFieldProps }) => {
  const [error, setError] = useState(false);
  const [helperTextState, setHelperTextState] = useState(helperText);

  useEffect(() => {
    let isValid = true;
    let errorText = '';

    switch (validationType) {
      case 'email':
        isValid = isValidEmail(value);
        errorText = 'Bitte gib eine gültige E-Mail-Adresse ein.';
        break;
      case 'number':
        isValid = isValidNumber(value);
        errorText = 'Bitte gib eine gültige Zahl ein.';
        break;
      case 'ip':
        isValid = isValidIP(value);
        errorText = 'Bitte gib eine gültige IP-Adresse ein.';
        break;
     
      default:
        isValid = true;
    }

    setError(!isValid && value !== '');
    setHelperTextState(!isValid && value !== '' ? errorText : helperText);
  }, [value, validationType, helperText]);

  return (
    <TextField
      label={label}
      variant="outlined"
      fullWidth
      error={error}
      helperText={helperTextState}
      value={value}
      onChange={onChange}
      {...textFieldProps}
    />
  );
};

export default TextBoxNormal;