import React from 'react';
import { Box } from '@mui/material';

const StyledBox = ({ children }) => {
  return (
    <Box
      sx={{
        border: 1, // Setzt die Dicke des Rahmens
        borderColor: 'grey.300', // Farbe des Rahmens
        backgroundColor: 'background.paper', // Nutzt die Papier-Hintergrundfarbe des Standard-Themas für einen leichten Kontrast
        borderRadius: 2, // Abrunden der Ecken
        p: 2, // Innenabstand (Padding) der Box
        boxShadow: 1, // Leichter Schatten für Tiefe
      }}
    >
      {children}
    </Box>
  );
};

export default StyledBox;
