import React from 'react';
import './App.css';
import { BrowserRouter, Route, Routes, useNavigate } from 'react-router-dom';
import Dashboard from './components/sites/dashboard/Dashboard';
import Login from './components/Login/Login';
import NavBar from "./components/NavBar/NavBar";
import Gateway from "./components/sites/gateway/gateway";
import Endpoints from "./components/sites/endpoints/endpoints";



function AppContent() {
    const navigate = useNavigate(); // Verwenden Sie useNavigate innerhalb einer Komponente, die in einem BrowserRouter eingebettet ist
    const token = localStorage.getItem('token');
    if (!token) {
        return <Login />;
    }

    function handleClick(site) {
        navigate(site); // Navigiere zur angegebenen Route
    }


    return (
        <>
            <div className="wrapper">
                <NavBar headertext="Filial-Manager" handleClick={handleClick} ></NavBar>
                <div className="content-wrapper">
                    <Routes>
                        <Route path="/web/dashboard" element={<Dashboard />} />
                        <Route path="/web/gateway" element={<Gateway />} />
                        <Route path="/web/endpoints" element={<Endpoints />} />
                      
                    </Routes>
                </div>
            </div>

        </>
    );
}

function App() {
    return (
        <div className="wrapper">
            <BrowserRouter>
                <AppContent />
            </BrowserRouter>
        </div>
    );
}

export default App;
