import React, { useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, IconButton } from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import AddBoxIcon from '@mui/icons-material/AddBox';
import Container from "../../Container/Container";
import Typography from '@mui/material/Typography';
import SpaceDivider from "../../mui/tools/divider";
import Styledbox from "../../mui/tools/styledbox";
import Endpoint from "./endpoint";
import LoadingSpinner from '../../LoadingSpinner/LoadingSpinner';
import { Alert, AlertTitle } from '@mui/material';
import { useEffect } from 'react';
import Button from '@mui/material/Button';
import { v4 as uuidv4 } from 'uuid';
import StringModal from '../../mui/modal/StringModal';
import CheckBox from '../../mui/checkbox/checkbox';
import SimpleModal from '../../mui/modal/Dialog';
import Footer from "../../Footer/Footer";






// Anfangsdaten, könnten auch von einem API-Aufruf stammen

const Endpoints = () => {
  

  const [services, setServices] = useState(null);
  const [alert, setAlert] = useState('');
  const [alertState, setAlertState] = useState(false);
  const [isEditorOpen, setIsEditorOpen] = useState(false);
  const [selectedEndpoint, setSelectedEndpoint] = useState(null);
  const [dialogAddBasic, setDialogAddBasic] = useState(false);

  const [open, setOpen] = useState(false);
  const [dialog, setDialog] = useState('');

  // Konfigurationsobjekt
  useEffect(() => {
    const fetchKonfiguration = async () => {
      try {
        const response = await fetch(localStorage.getItem('host') + '/config_service', {
          method: 'GET', 
          headers: {
            'Content-Type': 'application/json',
            'token': localStorage.getItem('token')
          }
        });
        if (!response.ok) {
          setAlertState(true);
          setAlert(`HTTP error! status: ${response.status}`);
          throw new Error(`HTTP error! status: ${response.status}`);
        } else {
          setAlertState(false);
          setAlert('');
        }
        const json = await response.json();
        setServices(json);
      } catch (error) {
        setAlertState(true);
        setAlert('Ein Fehler ist aufgetreten beim Abrufen der Endpunkte - no route');
        console.error("Ein Fehler ist aufgetreten beim Abrufen der Konfiguration:", error);
      }
    };

    fetchKonfiguration();
  }, []); // Leeres Array, um den Effekt nur beim ersten Rendern auszuführen

  if (!services) {
    return (<Container title=""><LoadingSpinner></LoadingSpinner>

      {alertState && (
        <p>
          <Alert severity="error">
            <AlertTitle>Error</AlertTitle>
            {alert}
          </Alert>
        </p>
      )}



    </Container>);
  } else {


    // Handler für Löschen
    const handleDeleteService = (serviceName) => {
      setServices(services.filter(service => service.UUID !== serviceName));
    };
    const handleDeleteEndpoint = (serviceName, endpointName) => {
      const updatedServices = services.map(service => {
        if (service.UUID === serviceName) {
          return { ...service, Endpoints: service.Endpoints.filter(endpoint => endpoint.UUID !== endpointName) };
        }
        return service;
      });
      setServices(updatedServices);

    };
    // Handler für Hinzufügen und Bearbeiten müssen implementiert werden basierend auf deinen Anforderungen
    const handleEditClick = (endpointData) => {
      setSelectedEndpoint(endpointData);
      setIsEditorOpen(true);
    };
    const HandleSave = () => {

      fetch(localStorage.getItem('host') + '/config_service', {
          method: 'POST',
          headers: {
              'Content-Type': 'application/json',
              'token': localStorage.getItem('token')
          },
          body: JSON.stringify(services),
      }).then(response => response.json())
          .then(data => {
              console.log('Success:', data);
              setDialog('Konfiguration gespeichert');
              setOpen(true);
          })
          .catch((error) => {
              console.error('Error:', error);
              setDialog('Fehler beim Speichern der Konfiguration');
              setOpen(true);
          });

  }
 
 
  const handleClose = () => {
    setDialog('');
    setOpen(false);
  };
  const handleAddBasic = (newEntry) => {
    setDialogAddBasic(false);
    const nservice =  {
      "Endpoints": [],
      "BasicEndpoint": {
        "Endpoint": "https://",
        "VerifySSL": true,
        "CertAuth": false,
        "Certs": {
          "CertPEM": "",
          "CertKEY": ""
        },
        "Active": false,
        "Name": "Basic",
        "UUID": uuidv4(),
        "OverrideTimeout": 0,
        "HeaderRouteMatches": [],
        "HeaderExists": [],
        "HeaderAdd": [],
        "JWTPreCheck": false,
        "JWTData": {
          "Header": "",
          "Key": "",
          "OnlySign": false,
          "Field": "",
          "Match": null
        },
        "HeaderReplace": []
      },
      "Active": false,
      "UUID": uuidv4(),
      "Name": newEntry
    }
    setServices([...services, nservice]);
    

   

  };
  const handleAddSubService = (uuid) => {
    const nservice =  {
      "Endpoint": "https://",
      "VerifySSL": true,
      "CertAuth": false,
      "Certs": {
        "CertPEM": "",
        "CertKEY": ""
      },
      "Active": false,
      "Name": "Sub",
      "UUID": uuidv4(),
      "OverrideTimeout": 0,
      "HeaderRouteMatches": [  {
        "Header": "system",
        "Value": "dev"
      }],
      "HeaderExists": [],
      "HeaderAdd": [],
      "JWTPreCheck": false,
      "JWTData": {
        "Header": "",
        "Key": "",
        "OnlySign": false,
        "Field": "",
        "Match": null
      },
      "HeaderReplace": []
    }
    const updatedServices = services.map(service => {
      if (service.UUID === uuid) {
        if (service.Endpoints) {
          return { ...service, Endpoints: [...service.Endpoints, nservice] };
        } else {
          return { ...service, Endpoints: [nservice] };
        }
      }
      return service;
    });
    setServices(updatedServices);
  };


  const HandleReload = async () => {

    try {
      const response = await fetch(localStorage.getItem('host') + '/reload', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'token': localStorage.getItem('token')
        }
      });
      if (!response.ok) {
       alert("Fehler");
        setDialog('Fehler beim Neuladen der Konfiguration - response bad', response.status);
        setOpen(true);
      } else {
        setDialog('Dast Gateway hat die Konfiguration neu geladen');
        setOpen(true);
        
      }
      
      
    } catch (error) {
      setDialog('Fehler beim Neuladen der Konfiguration - no route');
      setOpen(true);
      
    }

}
const handleEnableService = (uuid, active) => {
  const updatedServices = services.map(service => {
    if (service.UUID === uuid) {
      return { ...service, Active: active };
    }
    return service;
  });
  setServices(updatedServices);
};

const HandleOpenAdd = () => {

  setDialogAddBasic(true);

}

    const handleEditorSave = (updatedEndpoint) => {
      const updatedServices = services.map(service => {
        console.log('A ',service.BasicEndpoint.UUID, updatedEndpoint.UUID);
        if (service.BasicEndpoint.UUID === updatedEndpoint.UUID) {
          return { ...service, BasicEndpoint: updatedEndpoint };
        } else if (service.Endpoints) {
          const updatedEndpoints = service.Endpoints.map(endpoint => {
            console.log('B ',endpoint.UUID, updatedEndpoint.UUID);
            if (endpoint.UUID === updatedEndpoint.UUID) {
              return updatedEndpoint;
            }
            return endpoint;
          });
          return { ...service, Endpoints: updatedEndpoints };
        }
        return service;
      });
      setServices(updatedServices);
      setIsEditorOpen(false);
    };





    const handleEditorCancel = () => {
      setIsEditorOpen(false);
    };
    return (
      <Container title="Endpoints">
        <Typography variant="h4" gutterBottom>Services</Typography>
        <SpaceDivider />
        <Styledbox>
          {isEditorOpen ? (
            <Endpoint
              endpointData={selectedEndpoint}
              onSave={handleEditorSave}
              onCancel={handleEditorCancel}
            />
          ) : (
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell>Name</TableCell>
                    <TableCell>Endpoint</TableCell>
                    <TableCell>Active</TableCell>
                    <TableCell>Aktionen</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {services.map((service) => [
                    <TableRow key={service.BasicEndpoint.UUID} style={{ backgroundColor: '#E0F7FA' }}>
                      <TableCell>{service.Name} (Basic)</TableCell>
                      <TableCell>{service.BasicEndpoint.Endpoint}</TableCell>
                      <TableCell >
                      <CheckBox
                    label=""
                    checked={service.Active}
                    onChange={(e) => {
                      handleEnableService(service.UUID, e.target.checked)
                    }}
                />

                      </TableCell>
                      <TableCell>
                        <IconButton onClick={() => handleEditClick(service.BasicEndpoint)}>
                          <EditIcon />
                        </IconButton>
                        <IconButton onClick={() => handleDeleteService(service.UUID)}><DeleteIcon /></IconButton>
                        <IconButton onClick={() => handleAddSubService(service.UUID)}><AddBoxIcon /></IconButton>
                      </TableCell>
                    </TableRow>,
                    service.Endpoints.map((endpoint) => (
                      <TableRow key={endpoint.UUID} >
                        <TableCell> {service.Name} <b> - &gt; </b> {endpoint.Name}</TableCell>
                        <TableCell>{endpoint.Endpoint}</TableCell>
                        <TableCell></TableCell>
                        <TableCell>
                          <IconButton onClick={() => handleEditClick(endpoint)}>
                            <EditIcon />
                          </IconButton>
                          <IconButton onClick={() => handleDeleteEndpoint(service.UUID, endpoint.UUID)}><DeleteIcon /></IconButton>
                        </TableCell>
                      </TableRow>
                  
                    ))
                  ])}
                  <TableRow>
                  <TableCell>
                      <Button variant="contained" onClick={HandleSave}>Speichern</Button>
                    </TableCell>
                    <TableCell>
                      <Button variant="contained" onClick={HandleReload}>Reload Gateway</Button>
                    </TableCell>
                    <TableCell colSpan={4} align="right">
                    <Button variant="contained" onClick={HandleOpenAdd} >Add Main Endpoint</Button>
                    </TableCell>
                    
                  </TableRow>
                </TableBody>
              </Table>
            </TableContainer>
         
               
           
          )}
   
        </Styledbox>
        <SpaceDivider />
        {dialogAddBasic && (
         
            <StringModal
        modalcaption="Neuer Eintrag"
        onAdd={handleAddBasic}
      />
  
         
        )}


         
         <SimpleModal
         open={open}
         onClose={handleClose}
         caption="Hinweis"
         text={dialog}
       />

      
  
<Footer></Footer>
      </Container>

    )
  };
};






export default Endpoints;