import React, {useState} from 'react';
import { Button, Box, Container, Alert, AlertTitle} from '@mui/material';
import Logger from "../Logger/Logger";
import TextBox from '../mui/textbox/TextBoxNormal';
import logo from './logo.webp';
import './Login.css'
import Footer from '../Footer/Footer';
import LoginIcon from '@mui/icons-material/Login';








const logger = Logger("Login");

async function loginUser(credentials) {
 
    
    const etoken = ""
    const errcode = 500
    if (!credentials.username || !credentials.password)  return {etoken  ,errcode};
    const basicAuthString = 'Basic ' + btoa(credentials.username + ':' + credentials.password);
    const response = await fetch(localStorage.getItem('host') + '/login', {
        method: 'GET',
        headers: {
            'Content-Type': 'application/json',
            'Authorization': basicAuthString
        }
    });


    const statusCode = response.status;
    if (!response.ok) {
        logger.log("Login Fetch failed", response.json())
        return {etoken, statusCode};
    }
    const data = await response.json();
    let token = data.token;
    if (!token) {
        token = ""
    }
    logger.log("Login ok", response.json(), response.status)
    return {token, statusCode};

    
}



function LoginForm() {
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(false);
 
 const host = localStorage.getItem('host') 

 const divStyle = {
  backgroundImage: `url(${logo})`,
  width: '500px', 
  height: '500px', 
  backgroundSize: 'cover',
};

  const handleSubmit = async (e) => {
    e.preventDefault();
    const resp = await loginUser({ username: userName, password: password });
    console.log("Response", resp);
    if (resp.statusCode === 200) {
      logger.log("ALL OKAY");
      logger.log("Token", resp.token);
      localStorage.setItem('token', resp.token);
      window.location.replace("/web/dashboard");
    } else {
      logger.log("Login Error no Token retrieved!");
      setError(true); // Setze den error-Status auf true bei einem Fehler
    }
  };
  const setHostName = (val) => {
    localStorage.setItem('host', val);
  };

 const handleCloseAlert = () => {
  setError(false); 
    
  };

  return (
    <Container maxWidth="sm">
      <div style={divStyle}></div>
          {error && (
        <Box sx={{ mt: 2 }}>
          <Alert onClose={handleCloseAlert} severity="error">
            <AlertTitle>Error</AlertTitle>
            Login fehlgeschlagen! Bitte versuche es erneut...
          </Alert>
        </Box>
      )}
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          
        }}
      >

        <form onSubmit={handleSubmit} style={{ width: '100%' }}>
        
        <Box sx={{ mb: 2 }}>
            <TextBox
              fullWidth
              id="outlined-basic"
              label="Hostname"
                            variant="outlined"
              value={host}
              required
              onChange={(e) => setHostName(e.target.value)}
            />
          </Box>
          <Box sx={{ mb: 2 }}>
            <TextBox
              fullWidth
              id="outlined-basic"
              label="Benutzer"
                            variant="outlined"
              value={userName}
              required
              onChange={(e) => setUserName(e.target.value)}
            />
          </Box>
          <Box sx={{ mb: 2 }}>
            <TextBox
              fullWidth
              id="outlined-basic"
              label="Passwort"
              type="password"
              variant="outlined"
              value={password}
              required
              onChange={(e) => setPassword(e.target.value)}
            />
          </Box>
          <Button fullWidth variant="contained" type="submit" endIcon={<LoginIcon />}>
  Login
</Button>
      
        </form>
      </Box>
      <Footer></Footer>
    </Container>

  );
}

export default LoginForm;