import React, { useState } from 'react';
import { Button, IconButton, TextField, Dialog, DialogActions, DialogContent, DialogTitle, Table, TableBody, TableCell, TableRow, TableContainer, Paper, Typography } from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import AddIcon from '@mui/icons-material/Add';

const KeyValueEditor = ({ list, setList, modalCaption, caption }) => {
  const [newEntry, setNewEntry] = useState({ Header: '', Value: '' });
  const [open, setOpen] = useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setNewEntry({ Header: '', Value: '' });
  };

  const handleAddEntry = () => {
    if (newEntry.Header && newEntry.Value) {
      setList([...list, newEntry]);
      handleClose();
    }
  };

  const handleRemoveEntry = (indexToRemove) => {
    setList(list.filter((_, index) => index !== indexToRemove));
  };

  return (
    <div>
      <Typography variant="h8" style={{ margin: '10px' }}>{caption}</Typography>
      <TableContainer component={Paper} style={{ marginBottom: '10px' }}>
        <Table aria-label="list table" size="small">
          <TableBody>
            {list.map((entry, index) => (
              <TableRow key={index}>
                <TableCell>{entry.Header}</TableCell>
                <TableCell>{entry.Value}</TableCell>
                <TableCell align="right">
                  <IconButton edge="end" aria-label="delete" onClick={() => handleRemoveEntry(index)}>
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Button startIcon={<AddIcon />} onClick={handleClickOpen} variant="contained" size="small">
        Neuer Eintrag
      </Button>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>{modalCaption}</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Header"
            type="text"
            fullWidth
            variant="outlined"
            value={newEntry.Header}
            onChange={(e) => setNewEntry({ ...newEntry, Header: e.target.value })}
          />
          <TextField
            margin="dense"
            label="Value"
            type="text"
            fullWidth
            variant="outlined"
            value={newEntry.Value}
            onChange={(e) => setNewEntry({ ...newEntry, Value: e.target.value })}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Abbrechen</Button>
          <Button onClick={handleAddEntry}>Hinzufügen</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};

export default KeyValueEditor;
